'use client';

import React, { useState } from 'react';
import Icon from '@mdi/react';
import { 
  mdiArrowLeft, 
  mdiChevronDown,
  mdiClose
} from '@mdi/js';
import { colors } from '@/lib/design-tokens';
import clsx from 'clsx';

interface MobileMenuOrderingProps {
  onBack: () => void;
  menuName?: string;
  menus?: Array<{ name: string; entryPoint: string; }>;
}

interface MenuItem {
  id: string;
  name: string;
  description: string;
  price: number;
  image?: string;
}

interface MenuSection {
  id: string;
  name: string;
  items: MenuItem[];
}

// Canary-styled Button Component
interface CanaryButtonProps {
  children?: React.ReactNode;
  onClick?: () => void;
  variant?: 'primary' | 'outlined' | 'text' | 'icon';
  icon?: React.ReactNode;
  iconPosition?: 'left' | 'right';
  className?: string;
}

const CanaryButton: React.FC<CanaryButtonProps> = ({ 
  children, 
  onClick, 
  variant = 'primary',
  icon,
  iconPosition = 'left',
  className = ''
}) => {
  const baseClasses = "inline-flex items-center justify-center font-roboto font-medium transition-all duration-200 outline-none";
  
  const variantClasses = {
    primary: "bg-blue-600 text-white hover:bg-blue-700 h-10 px-4 rounded",
    outlined: "bg-white border border-gray-300 text-gray-700 hover:bg-gray-50 h-10 px-4 rounded",
    text: "bg-transparent text-gray-700 hover:text-black h-10 px-4",
    icon: "bg-transparent text-gray-700 hover:text-black w-10 h-10 rounded-full hover:bg-gray-100"
  };
  
  const iconClasses = variant === 'icon' ? "w-6 h-6" : "w-5 h-5";
  const spacing = variant === 'icon' ? "" : (iconPosition === 'left' ? "mr-2" : "ml-2");
  
  return (
    <button 
      onClick={onClick}
      className={clsx(baseClasses, variantClasses[variant], className)}
    >
      {icon && iconPosition === 'left' && (
        <span className={clsx(iconClasses, spacing)}>{icon}</span>
      )}
      {variant !== 'icon' && children}
      {icon && iconPosition === 'right' && (
        <span className={clsx(iconClasses, spacing)}>{icon}</span>
      )}
    </button>
  );
};

// Canary-styled Select Component
interface CanarySelectProps {
  options: Array<{ label: string; value: string }>;
  value?: string;
  onChange?: (event: React.ChangeEvent<HTMLSelectElement>) => void;
  placeholder?: string;
}

const CanarySelect: React.FC<CanarySelectProps> = ({ 
  options, 
  value, 
  onChange, 
  placeholder 
}) => {
  return (
    <div className="relative">
      <select
        value={value}
        onChange={onChange}
        className="w-full h-12 px-4 pr-10 bg-white border border-gray-300 rounded font-roboto text-sm text-gray-900 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 appearance-none"
      >
        {placeholder && (
          <option value="" disabled>
            {placeholder}
          </option>
        )}
        {options.map((option) => (
          <option key={option.value} value={option.value}>
            {option.label}
          </option>
        ))}
      </select>
      <div className="absolute right-3 top-1/2 -translate-y-1/2 pointer-events-none">
        <Icon path={mdiChevronDown} size={0.8} color={colors.black3} />
      </div>
    </div>
  );
};

export const MobileMenuOrdering: React.FC<MobileMenuOrderingProps> = ({ 
  onBack, 
  menuName = "Breakfast menu",
  menus = []
}) => {
  const [selectedMenuName, setSelectedMenuName] = useState(menuName);
  const [selectedSection, setSelectedSection] = useState('cold');
  
  // Convert menus to CanarySelect options format
  const menuOptions = menus.map(menu => ({
    label: menu.name,
    value: menu.name
  }));

  // Sample menu sections - since item creation flow hasn't been built yet, we'll show empty sections
  const menuSections: MenuSection[] = [
    { id: 'cold', name: 'Cold', items: [] },
    { id: 'mains', name: 'Mains', items: [] },
    { id: 'beverages', name: 'Beverages', items: [] }
  ];

  const handleMenuSelect = (event: React.ChangeEvent<HTMLSelectElement>) => {
    setSelectedMenuName(event.target.value);
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-gray-100 p-8">
      {/* Back to Mobile Landing Button */}
      <div className="fixed top-8 left-8 z-50">
        <CanaryButton
          onClick={onBack}
          variant="outlined"
          icon={<Icon path={mdiArrowLeft} size={0.8} />}
          iconPosition="left"
        >
          Back to Mobile Landing
        </CanaryButton>
      </div>

      {/* Mobile Frame */}
      <div 
        className="relative bg-white overflow-hidden shadow-xl"
        style={{
          width: '430px',
          height: '932px',
          borderRadius: '44px',
          boxShadow: '0px 8px 16px 0px rgba(0,0,0,0.16)'
        }}
      >
        {/* Navigation Header */}
        <div className="flex items-center justify-between px-4 py-3 bg-white border-b border-neutral-200">
          <CanaryButton 
            onClick={onBack}
            variant="icon"
            icon={<Icon path={mdiArrowLeft} size={1} />}
          />
          
          <h1 
            className="font-roboto text-[16px] font-semibold text-center"
            style={{ color: colors.black1 }}
          >
            In-room dining
          </h1>
          
          <div className="w-10 h-10" />
        </div>

        {/* Menu Dropdown */}
        <div className="px-4 py-4 border-b border-neutral-200">
          <CanarySelect
            options={menuOptions}
            value={selectedMenuName}
            onChange={handleMenuSelect}
            placeholder="Select a menu"
          />
        </div>

        {/* Menu Title */}
        <div className="px-4 py-4">
          <h2 
            className="font-roboto text-[24px] font-semibold"
            style={{ color: colors.black1 }}
          >
            {selectedMenuName}
          </h2>
        </div>

        {/* Section Tabs */}
        <div className="px-4 pb-4">
          <div className="flex border-b border-neutral-200">
            {menuSections.map((section) => (
              <CanaryButton
                key={section.id}
                onClick={() => setSelectedSection(section.id)}
                variant="text"
                className={`flex-1 border-b-2 transition-colors !rounded-none ${
                  selectedSection === section.id
                    ? 'border-black !text-black'
                    : 'border-transparent !text-gray-600 hover:!text-black'
                }`}
              >
                {section.name}
              </CanaryButton>
            ))}
          </div>
        </div>

        {/* Menu Items Section */}
        <div className="flex-1 px-4 pb-4 overflow-y-auto">
          {(() => {
            const currentSection = menuSections.find(section => section.id === selectedSection);
            
            if (!currentSection || currentSection.items.length === 0) {
              return (
                <div className="flex flex-col items-center justify-center py-16 text-center">
                  <div 
                    className="w-16 h-16 rounded-full mb-4 flex items-center justify-center"
                    style={{ backgroundColor: colors.black6 }}
                  >
                    <Icon path={mdiClose} size={1.5} color={colors.black4} />
                  </div>
                  <h3 
                    className="font-roboto text-[18px] font-semibold mb-2"
                    style={{ color: colors.black1 }}
                  >
                    No items in {currentSection?.name}
                  </h3>
                  <p 
                    className="font-roboto text-[14px] max-w-64"
                    style={{ color: colors.black4 }}
                  >
                    This section doesn't have any items yet. Check back later for delicious options!
                  </p>
                </div>
              );
            }

            return (
              <div className="space-y-4">
                {currentSection.items.map((item) => (
                  <div key={item.id} className="flex items-start gap-3 p-3 border border-neutral-200 rounded-lg">
                    {item.image && (
                      <img 
                        src={item.image} 
                        alt={item.name}
                        className="w-16 h-16 rounded-lg object-cover"
                      />
                    )}
                    <div className="flex-1">
                      <h4 
                        className="font-roboto text-[16px] font-semibold mb-1"
                        style={{ color: colors.black1 }}
                      >
                        {item.name}
                      </h4>
                      <p 
                        className="font-roboto text-[14px] mb-2"
                        style={{ color: colors.black4 }}
                      >
                        {item.description}
                      </p>
                      <span 
                        className="font-roboto text-[16px] font-semibold"
                        style={{ color: colors.black1 }}
                      >
                        ${item.price.toFixed(2)}
                      </span>
                    </div>
                  </div>
                ))}
              </div>
            );
          })()}
        </div>
      </div>

    </div>
  );
};